
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
;
import sha512 from 'js-sha512' //sha512加密
export default {
	data() {
		return {
			passwordType: 'password',
			passwordType2: 'password',
			oldPassword: "",
			password: '',
			password2: ''
		}
	},
	computed: {
		//判断是否与旧密码相同
		likeOldPassword() {
			return this.oldPassword == this.password ? true : false
		},
		//判断两次密码是否相同
		isEqually() {
			return this.password == this.password2 ? true : false
		}
	},
	methods: {
		passwordTypeChange() {
			this.passwordType = this.passwordType === 'password' ? 'text' : 'password'
		},
		passwordTypeChange2() {
			this.passwordType2 = this.passwordType2 === 'password' ? 'text' : 'password'
		},
		//提交表单
		editPassword() {
			if(!this.likeOldPassword) {
				if(this.isEqually) {
					this.$vux.confirm.show({
						title: '提示',
						content: '确认修改？',
						onConfirm: () => {
							this.$common.ajax({
								data: {
									header: {
										action: 'EditDoctorPassword',
									},
									body: {
										doctorid: this.$common.getLocalStorage('user').doctorid,
										oldpassword: sha512(this.oldPassword),
										newpassword: sha512(this.password)
									}
								},
								success: (res) => {
									this.$vux.toast.show({
										text: '修改成功',
										onHide: () => {
											this.$router.go(-1);
										}
									});
								}
							})
						}
					})
				} else {
					this.$vux.toast.show({
						text: '两次密码输入不一致',
						type: 'text'
					});
				}
			} else {
				this.$vux.toast.show({
					text: '新密码不能与旧密码相同',
					type: 'text'
				});
			}
		}
	},
	mounted() {
		this.$validator.validateAll()
	},
	components: {
		XInput,
		Group,
		XButton,
		XHeader
	}
}



