
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
	data() {
		return {
			type: {
				active: '0',
				list:[{
					value:'程序漏洞',
					key:1
				},{
					value:'使用问题',
					key:2
				},{
					value:'新功能需求',
					key:3
				},{
					value:'性能问题',
					key:4
				},{
					value:'其他',
					key:5
				}]
			},
			state: false, //提交状态
			content: '',
			tel: ''
		}
	},
	methods: {
		toggleClass(obj) {
			this.type.active = obj.key;
		},
		//提交表单
		submitData() {
			this.$common.ajax({
				data: {
					header: {
						action: 'AddFeedback',
					},
					body: {
						Platform:32,
						doctorid: this.$common.getLocalStorage('user').doctorid,
						userid:0,
						type:this.type.active,
						Content:  this.content,
						Mobile: this.tel
					}
				},
				success: (res) => {
					this.state = true;
					setTimeout(() => {
						this.$router.go(-1);
					}, 2000)

				}
			})
		}
	},
	mounted() {
		this.$validator.validateAll()
	},

	components: {
		Group,
		XTextarea,
		XInput,
		XHeader,
		XButton
	}
}



