
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import mixins from 'assets/js/mixins.js'
import dateTime from 'components/dateTime'
export default {
	name: 'appointment',
	mixins: [mixins.scroller],
	data() {
		return {
			addVisitActive: {}, //当前点击的记录
			confirm: false,
			applyText: '', //备注
			visitState: [{
				name: '全部',
				value: ''
			}, {
				name: '未就诊',
				value: 0
			}, {
				name: '已就诊',
				value: 1
			}, {
				name: '过期',
				value: 2
			}, {
				name: '已取消',
				value: 3
			}],
			visitValue: [],
			state: {
				0: 'warn', //未就诊
				1: 'success', //已就诊
				2: 'overdue', //过期
				3: 'cancel' //已取消
			}
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetAppointmentList',
				},
				body: {
					DoctorID: this.$common.getLocalStorage('user').doctorid,
					State: this.visitValue[0],
					timetype:'1',
					starttime: this.$refs.dateTime.startTime,
					endtime: this.$refs.dateTime.endTime
				}
			}
		}
	},
	methods: {
		hideConfirm(){
			this.applyText='';
		},
		showConfirm(opt) {
			if(opt.StateID == 0) {
				this.confirm = true;
				this.addVisitActive = opt;
			}
		},
		addVisit() {
			this.$common.ajax({
				data: {
					header: {
						action: 'AddVisit',
					},
					body: {
						UserID: this.addVisitActive.UserID,
						DoctorID: this.$common.getLocalStorage('user').doctorid,
						AppointmentID: this.addVisitActive.ID,
						VisitTime: dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss'),
						Content: this.applyText
					}
				},
				success: (res) => {
					this.$vux.toast.show({
						text: '添加成功'
					});
					this.getData();

				}
			})
		},
	},
	mounted() {
	},
	components: {
		XHeader,
		Spinner,
		dateTime,
		PopupPicker,
		Confirm,
		XTextarea
	}
}



